<template>
  <div>
    <div>
      <div>:<input v-model="toPath" /></div>
      <div>
        <span>
          <input
            ref="file"
            class="fileUploaderClass"
            type="file"
            name="file"
            multiple
            @change.stop="uploadFiles()"
        /></span>
        <span>
          <input
            ref="folders"
            class="fileUploaderClass"
            type="file"
            name="folders"
            webkitdirectory
            multiple
            @change.stop="uploadFolders()"
        /></span>
      </div>
      <div style="text-align: left">
        <span>:/{{ path }}</span>
      </div>
    </div>
    <button @click="uploadFile()">Upload</button>
    <hr />
    <div v-if="fileList">
      <p v-for="(item, index) in fileList" :key="index">
        {{ item.webkitRelativePath || item.name }}
      </p>
    </div>
    <ul class="fileList">
      <li v-for="file in diskFiles" :key="file.name">
        <font-awesome-icon :icon="typeIcon(file)" fixed-width />
        <span>{{ file.name }}</span>
        <input
          v-if="!file.isFile"
          name="path"
          type="radio"
          @click="toPath = file.name"
        />
      </li>
    </ul>
  </div>
</template>

<script>
import axios from "axios";
import $ from "jquery";
export default {
  data: () => {
    return {
      fileList: [],
      diskFiles: [],
      path: "",
      toPath: "",
    };
  },
  components: {},
  mounted: function () {
    this.loadDiskFiles();
  },

  computed: {},
  methods: {
    typeIcon(file) {
      if (!file.isFile) return "folder";
      else {
        if (file.name.indexOf(".mp4")) return "video";
        return "file";
      }
    },
    methodToRunOnSelect(payload) {
      this.object = payload;
    },
    uploadFiles() {
      console.log(this.$refs.file.files);
      this.fileList = this.$refs.file.files; // filelist
    },
    uploadFolders() {
      console.log(this.$refs.folders.files);
      this.fileList = this.$refs.folders.files; // filelist
    },
    loadDiskFiles() {
      $.getJSON("/api/listDisk").then((data) => {
        this.diskFiles.length = 0;
        console.log(data);
        this.diskFiles.push(...data.contents);
      });
    },
    uploadFile() {
      let formData = new window.FormData();
      formData.append("path", this.path);

      for (let i = 0; i < this.fileList.length; i++) {
        // file appendformdata
        formData.append("file", this.fileList[i]);
      }
      let url = "/api/upload";
      axios({
        method: "post",
        url: url,
        data: formData,
        headers: { "Content-Type": "multipart/form-data" },
      }).then(function (res) {
        console.log(res);
      });
    },
  },
  watch: {},
};
</script>

<style scoped>
.fileList li {
  display: flex;
  text-align: left;
}
.fileList li *:nth-child(2) {
  flex-grow: 1;
}
</style>